import Vue from "vue";
import App from "./App";
import router from "./router";
require("!style-loader!css-loader!less-loader!./assets/less/common.less"); //less
//
import i18n from "./i18n/i18n";

// 
import http from "./assets/js/http";
Vue.prototype.http = http;

//vuex
import store from "./store/index";

//mock
// require('./mock')

//element-ui
import {
    Pagination,
    Dialog,
    Rate,
    Form,
    FormItem,
    Input,
    Select,
    Option,
    Radio,
    Loading,
    Message,
    MessageBox
} from "element-ui";
//
Vue.use(Pagination);
//
Vue.use(Dialog);
//
Vue.use(Rate);
//
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Input);
//
Vue.use(Select)
Vue.use(Option)
Vue.use(Radio);
// //
Vue.use(Loading);
Vue.prototype.$confirm = MessageBox.confirm;

Vue.prototype.$message = Message;

//
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
    el: "#app",
    router,
    i18n,
    store,
    components: {
        App
    },
    template: "<App/>"
});



